        </v-card-text>
        <!-- 加载环 -->
        <v-overlay :value="overlay">
          <v-progress-circular indeterminate color="primary"></v-progress-circular>
          <span>{{loading_info}}</span>
        </v-overlay>
      </v-card>
    </v-navigation-drawer>

    <!-- message弹窗 -->
    <v-snackbar v-model="snackbar.show" top :color="snackbar.color" app>
      {{ snackbar.message }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar.show = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </template>
    </v-snackbar>

    <!-- 底部抽屉 -->
    <v-navigation-drawer
      v-model="bottomDrawer.showBottomDrawer"
      dark
      bottom
      fixed
      :style="bottomDrawer.type==0?'height:24% ;z-index:99':'height:16% ;z-index:99'"
      touchless
    >
      <div>
        <v-btn
          text
          outlined
          width="100%"
          height="8vh"
          @click="bottomDrawer.type==0?dislikePost(bottomDrawer.id):disLike(bottomDrawer.id)"
        >
          <v-icon>mdi-alert-decagram-outline</v-icon>
          <span class="ml-2">举报</span>
          <span class="ml-1 text_xsmall grey--text">【举报数:{{bottomDrawer.dislike_num}}】</span>
        </v-btn>
        <v-btn
          text
          outlined
          width="100%"
          height="8vh"
          v-if="bottomDrawer.type==1"
          @click="shieldUser(bottomDrawer.commentUserId)"
        >
          <v-icon>mdi-close-box-multiple-outline</v-icon>
          <span class="ml-2">贴内屏蔽该用户</span>
        </v-btn>
        <v-btn
          text
          outlined
          width="100%"
          height="8vh"
          v-if="bottomDrawer.type==0"
          @click="shieldPost()"
        >
          <v-icon>mdi-close-box-multiple-outline</v-icon>
          <span class="ml-2">屏蔽</span>
        </v-btn>
        <v-btn
          text
          outlined
          width="100%"
          height="8vh"
          @click="addCollect(bottomDrawer.id)"
          v-if="bottomDrawer.type==0"
        >
          <v-icon>mdi-bookmark-multiple-outline</v-icon>
          <span class="ml-2">收藏</span>
        </v-btn>
      </div>
    </v-navigation-drawer>
    <v-dialog
      v-model="shieldExplainDialog"
      scrollable
      :overlay="false"
      max-width="500px"
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <v-icon>mdi-help-circle</v-icon>
          <span class="ml-2">评论折叠解释</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-2">
          <div v-html="shieldCommentExContent"></div>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
let originCommentEdit = {
  commentDialog: false,
  wantToCommentTo: 0, //这个是楼层数
  wantToCommentToId: 0, //这个是评论id,0代表回复楼主
  contentRule: [v => v.length <= 200 || "Max 25 characters"],
  avatar: -1,
  content: "",
  img: "",
  file: [],
};
import qs from "qs";
import MescrollVue from "mescroll.js/mescroll.vue";
import VuePoll from "vue-poll";
import touch from "../utils/touch"
import Authorization from '@/utils/Authorization'

var shieldComment = (data, shieldCommentUserId) => {
  try {
    if (localStorage.getItem('shieldCommentUserId'))
      var shieldCommentUserId = JSON.parse(localStorage.getItem('shieldCommentUserId'))
  } catch (e) {
    var shieldCommentUserId = undefined
  }
  if (!shieldCommentUserId) return data;
  data.forEach(item => {
    if (shieldCommentUserId.includes(item.commentUserId))
      item.filterLevel = 2
  })
  return data;
}


export default {
  name: "postDetail",
  components: {
    MescrollVue, // 注册mescroll组件
    VuePoll
  },
  data() {
    return {
      loadMethod: 1,//1是老的在上面,2是新的在上面
      commentRecently: false,
      thread_start: {},
      isUp: false,
      isTop: false,
      //下拉刷新的配置
      mescrollDown: {
        callback: this.refreshData,
        use: true
      },
      //上拉加载
      mescrollUp: {
        callback: this.getMorePosts,
        onScroll: this.onScroll,
        noMoreSize: 1,

      },
      overlay: false,
      if_list_bottom: false, //评论加载是否到底了
      atTop: true, //是否在顶部
      loading_info: "",
      snackbar: {
        show: false,
        message: "",
        color: ""
      },
      img: {
        picDialog: false,
        showImgUrl: ""
      },
      comment_to: 1,
      //回复楼层显示
      comment_to_show: {
        comment_toDialog: false
      },
      //回复编辑
      commentEdit: {
        commentDialog: false,
        wantToCommentTo: 0, //这个是楼层数
        wantToCommentToId: 0, //这个是评论id,0代表回复楼主
        contentRule: [v => v.length <= 200 || "Max 25 characters"],
        avatar: -1,
        content: "",
        img: "",
        file: [],
        update: true //用于刷新文件上传组件
      },
      //头像选择
      avatarsChoose: {
        avatars: [],
        avatarsChoosed: -1
      },
      //我的身份
      myInfo: undefined,
      hot_comments: [],
      comments: [],
      options: {
        question: "",
        answers: []
      },
      bottomDrawer: {
        showBottomDrawer: false,
        id: 0,
        commentUserId: 0,
        dislike_num: 0,
        type: 1
      },
      shieldExplainDialog: false,
      loadMethods: [{
        text: '按回复时间',
        icon: 'mdi-arrow-up'
      }, {
        text: '按回复时间',
        icon: 'mdi-arrow-down'
      }, {
        text: '只看楼主',
        icon: 'mdi-account'
      }],
      shieldCommentExContent: sessionStorage.getItem('shieldCommentExContent')
    };
  },

  async created() {
    let that = this;
    //cordova监听键盘
    window.addEventListener("native.keyboardshow", function (e) {
      that.$store.change_keyboard(true);
    });
    window.scrollTo(0, 0);
    if (Object.keys(this.$route.query).length == 1) {
      this.thread_start = await this.getThreadStart();
    } else this.thread_start = this.$route.query;
    //如果有投票
    if (this.thread_start.vote != null) {
      that.$request('get', '/forum/getVote', {
        timeToken: new Date().getTime(),
        postId: that.thread_start.id,
        voteId: that.thread_start.vote
      }, {})
        .then(res => {
          that.options.question = res.data[0].question;
          that.options.answers = res.data.map(item => {
            return {
              value: item.voteAnswerId,
              text: item.answer,
              votes: item.num
            };
          });
        })
        .catch(e => {
          console.log(e)
        })
      that.$request('get', '/forum/ifVote', { timeToken: new Date().getTime(), voteId: that.thread_start.vote })
        .then(res => {
          that.$nextTick(() => {
            that.options.showResults = res.data != 0;
          });
        })
        .catch(e => {
          that.showSnackbar(e);
        })
    }
  },
  mounted() { },

  methods: {
    random(i) {
      return Math.floor(Math.random() * 1000 + i)
    },

    changeLoadMethod() {
      if (this.loadMethod < 3) this.loadMethod++;
      else this.loadMethod = 1
    },
    getThreadStart() {
      let that = this;
      return new Promise((resolve, reject) => {
        that.$request('get', '/forum/getThreadStart', { timeToken: new Date().getTime(), postId: that.$route.query.id }, {})
          .then(res => {
            resolve(res.data[0])
          })
          .catch(e => {
            reject(e.data)
            that.showSnackbar(e)
          })
      });
    },

    addVote(obj) {
      let that = this
      that.$request('post', '/forum/addVote', { timeToken: new Date().getTime() }, {
        voteId: that.thread_start.vote,
        value: obj.value
      })
        .then(res => {
          that.showSnackbar(res, "ok");
        })
        .catch(e => {
          that.showSnackbar(e)
        })
    },

    onScroll(mescroll, y, isUp) {
      if (y == 0) this.isTop = true;
      else this.isTop = false;
      this.isUp = isUp;
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll
    },
    refreshData(mescroll) {
      this.commentRecently = false
      mescroll.resetUpScroll();
      let that = this;
      //增加访问次数
      that.$request('get', '/forum/updateVisitNum', {
        postId: that.thread_start.id,
      })
      //获取热门评论
      that.$request('get', '/forum/getHotComments', {
        timeToken: 0,
        postId: that.thread_start.id,
        type: that.loadMethod,
      })
        .then(res => {
          shieldComment(res.data)
          that.hot_comments = res.data;
          that.$request('get', '/forum/getComments', {
            timeToken: this.loadMethod != 2 ? 0 : new Date().getTime(),
            postId: that.thread_start.id,
            type: this.loadMethod
          })
            .then(res => {
              shieldComment(res.data)
              that.comments = res.data;
              mescroll.endSuccess();
            })
            .catch(e => {
              that.showSnackbar(e);
              mescroll.endErr();
            })
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e);
          mescroll.endErr();
        })
    },
    getMorePosts(page, mescroll) {
      let that = this;
      let loadMethod = this.loadMethod
      if (this.commentRecently) loadMethod = 2
      let time = loadMethod != 2 ? 0 : new Date().getTime()
      let latest_comment_time =
        (that.comments.length > 0 && page.num != 1)
          ? that.comments[that.comments.length - 1].created_time
          : time;
      that.$request('get', '/forum/getComments', {
        timeToken: new Date(latest_comment_time).getTime(),
        postId: that.thread_start.id,
        type: loadMethod,
      })
        .then(res => {
          if (page.num === 1) that.comments = []
          shieldComment(res.data)
          that.comments.push(...res.data);
          that.$nextTick(() => {
            mescroll.endSuccess(res.data.length);
          });
        })
        .catch(e => {
          that.showSnackbar(e);
          mescroll.endErr();
        })
    },
    like(comment_id) {
      let that = this;
      console.log(that.comments)
      that.$request('post', '/forum/commentLikeOrDislike', {}, {
        commentId: comment_id,
        value: 1
      })
        .then(res => {
          that.showSnackbar(res, 'ok');
          that.comments.forEach(item => {
            if (item.comment_id == comment_id) item.like_num++;
          })
          that.hot_comments.forEach(item => {
            if (item.comment_id == comment_id) item.like_num++;
          })
        })
        .catch(e => {
          that.showSnackbar(e)
        })
    },
    disLike(comment_id) {
      console.log(this.comments)
      let that = this;
      that.$request('post', '/forum/commentLikeOrDislike', {}, {
        commentId: comment_id,
        value: -1
      })
        .then(res => {
          that.showSnackbar(res, 'ok');
          that.bottomDrawer.dislike_num++;
        })
        .catch(e => {
          that.showSnackbar(e)
        })
    },
    dislikePost(id) {
      let that = this;
      that.$request('post', '/forum/forumLikeOrDislike', {}, {
        postId: id,
        value: -1
      })
        .then(res => {
          that.showSnackbar(res, "ok");
          that.bottomDrawer.dislike_num++;
        })
        .catch(e => {
          that.showSnackbar(e)
        })
    },
    addCollect(id) {
      let that = this;
      that.$request('post', '/forum/addCollect', {}, { postId: Number(id) })
        .then(res => {
          that.showSnackbar(res, "ok");
        })
        .catch(e => {
          that.showSnackbar(e)
        })
    },
    shieldUser(commentUserId) {
      try {
        if (localStorage.getItem('shieldCommentUserId')) {
          let shieldCommentUserId = JSON.parse(localStorage.getItem('shieldCommentUserId'))
          if (shieldCommentUserId.includes(commentUserId)) {
            this.showSnackbar('已经屏蔽过了')
            return;
          }
          shieldCommentUserId.push(commentUserId)
          localStorage.setItem('shieldCommentUserId', JSON.stringify(shieldCommentUserId))
          this.showSnackbar('屏蔽成功!请刷新', 'ok', 2200)
        }
        else {
          let shieldCommentUserId = []
          shieldCommentUserId.push(commentUserId)
          localStorage.setItem('shieldCommentUserId', JSON.stringify(shieldCommentUserId))
          this.showSnackbar('屏蔽成功!请刷新', 'ok', 2200)
        }


      } catch (e) {
        localStorage.removeItem('shieldCommentUserId')
        this.showSnackbar('屏蔽失败!')
      }

    },
    shieldPost() {
      try {
        if (localStorage.getItem('shieldPostId')) {
          let shieldPostId = JSON.parse(localStorage.getItem('shieldPostId'))
          if (shieldPostId.includes(this.thread_start.id)) {
            this.showSnackbar('已经屏蔽过了')
            return;
          }
          shieldPostId.push(this.thread_start.id)
          localStorage.setItem('shieldPostId', JSON.stringify(shieldPostId))
          this.showSnackbar('屏蔽成功!请返回后下拉刷新', 'ok', 1500);
          setTimeout(() => {
            history.go(-1);
          }, 1000);
        }
        else {
          let shieldPostId = []
          shieldPostId.push(this.thread_start.id)
          localStorage.setItem('shieldPostId', JSON.stringify(shieldPostId))
          this.showSnackbar('屏蔽成功!请返回后下拉刷新', 'ok', 1500);
          setTimeout(() => {
            history.go(-1);
          }, 1000);
        }

      } catch (e) {
        localStorage.removeItem('shieldPostId')
        this.showSnackbar('屏蔽失败!')
      }

    },
    openCommentDialog(to) {
      let that = this;
      if (to == 0) {
        that.commentEdit.wantToCommentTo = 0;
        that.commentEdit.wantToCommentToId = 0;
      } else {
        that.commentEdit.wantToCommentTo = to.thread_num;
        that.commentEdit.wantToCommentToId = to.comment_id;
      }
      //获取个人信息

      that.$request('get', '/user/getUserInfoInPost', {
        date: new Date().getTime(),
        postId: that.thread_start.id
      })
        .then(res => {
          if (res.data.length == 0) {
            //说明没有评论过,加载头像
            let that = this;
            that.$request('get', '/user/getAvatar', {
              date: new Date().getTime(),
              postId: that.thread_start.id,
              num: 6
            })
              .then(res => {
                that.avatarsChoose.avatars = res.data;
              });
            that.commentEdit.commentDialog = true;
          } else {
            that.commentEdit.commentDialog = true;
            that.myInfo = res.data[0];
            that.commentEdit.avatar = res.data[0].avatar;
          }
        })
        .catch(e => {
          console.log(e);
          that.showSnackbar(e);
        });
    },
    imageChange(file) {
      this.commentEdit.file = file;
    },
    submit() {
      let that = this;
      if (this.commentEdit.content == "")
        that.showSnackbar({ data: "回复内容不能为空" });
      else if (this.commentEdit.content.length > 200)
        that.showSnackbar({ data: "回复不能超过200字" });
      else if (this.commentEdit.avatar == -1)
        that.showSnackbar({ data: "请选择头像" });
      else if (this.commentEdit.file.length > 1)
        that.showSnackbar({ data: "最多只能上传一张图片" });
      else {
        //有图片的情况
        if (that.commentEdit.file.length == 1) {
          this.overlay = true;
          this.loading_info = "正在上传图片";
          let img = this.commentEdit.file[0];
          const data = new FormData();
          data.append("img", img);
          data.append("format", "json");
          this.$axios
            .post(that.$global.url + "/edit/imageUpload", data, {
              headers: {
                "Content-Type": "multipart/form-data",
                token: localStorage.getItem("token"),
                auth: Authorization.f()
              }
            })
            .then(res => {
              res = res.data;
              if (res.status != "ok") {
                that.overlay = false;
                this.showSnackbar(res);
              } else {
                that.loading_info = "正在提交";
                that.showSnackbar(res, 'ok')
                that.commentEdit.img = res.data;
                that.$request('post', "/edit/commentUpload", {}, {
                  postId: that.thread_start.id,
                  name: that.myInfo ? that.myInfo.name : null,
                  content: that.commentEdit.content,
                  img: that.commentEdit.img,
                  avatar: that.commentEdit.avatar,
                  wantToCommentTo: that.commentEdit.wantToCommentToId
                })
                  .then(res => {
                    that.overlay = false;
                    that.showSnackbar(res, 'ok')
                    // 移除组件
                    that.commentEdit.update = false;
                    // 在组件移除后，重新渲染组件
                    // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
                    that.$nextTick(() => {
                      //还原提交框
                      that.commentEdit = {
                        commentDialog: false,
                        wantToCommentTo: 0, //这个是楼层数
                        wantToCommentToId: 0, //这个是评论id,0代表回复楼主
                        contentRule: [v => v.length <= 200 || "Max 25 characters"],
                        avatar: -1,
                        content: "",
                        img: "",
                        file: []
                      };
                      that.commentEdit.update = true;
                    });
                    //关闭弹窗
                    that.commentEdit.commentDialog = false;
                    setTimeout(() => {
                      that.commentRecently = true
                      that.addComments(that.mescroll)
                    }, 1000);
                  })
                  .catch(e => {
                    that.overlay = false;
                    this.showSnackbar(e);
                  })
              }
            })
            .catch(e => {
              console.log(e)
              that.showSnackbar(e)
            })
        }
        //没有图片则直接上传
        else {
          console.log(that.commentEdit);
          that.$request('post', '/edit/commentUpload', {}, {
            postId: that.thread_start.id,
            name: that.myInfo ? that.myInfo.name : undefined,
            content: that.commentEdit.content,
            img: undefined,
            avatar: that.commentEdit.avatar,
            wantToCommentTo: that.commentEdit.wantToCommentToId
          })
            .then(res => {
              that.overlay = false;
              that.showSnackbar(res, 'ok')
              // 移除组件
              that.commentEdit.update = false;
              // 在组件移除后，重新渲染组件
              // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
              that.$nextTick(() => {
                //还原提交框
                that.commentEdit = {
                  commentDialog: false,
                  wantToCommentTo: 0, //这个是楼层数
                  wantToCommentToId: 0, //这个是评论id,0代表回复楼主
                  contentRule: [v => v.length <= 200 || "Max 25 characters"],
                  avatar: -1,
                  content: "",
                  img: "",
                  file: []
                };
                that.commentEdit.update = true;
              });
              //关闭弹窗
              that.commentEdit.commentDialog = false;
              setTimeout(() => {
                this.commentRecently = true
                that.addComments(that.mescroll)
              }, 1200);
            })
            .catch(e => {
              that.overlay = false;
              that.showSnackbar(e);
            })
        }
      }
    },
    addComments(mescroll) {
      mescroll.resetUpScroll();
      let that = this;
      that.$request('get', '/forum/getComments', {
        timeToken: new Date().getTime() + 100000,
        postId: that.thread_start.id,
        type: 2
      })
        .then(res => {
          shieldComment(res.data)
          that.comments = res.data;

          mescroll.scrollTo(0, 0);
        })
        .catch(e => {
          that.showSnackbar('获取新评论失败');
          mescroll.endErr();
        })

    },

    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = "";
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date
          .toJSON()
          .substr(0, 16)
          .replace("T", " ");
      }
      return res;
    },
    swipe(direction) {
      this.swipeDirection = direction
      console.log(direction)
    },
    rightSlide() {
      this.$router.go(-1)
    },
    leftSlide() {
      console.log('左滑')
    }

  },
  watch: {
    "commentEdit.commentDialog": function (newVal) {
      this.$store.change_drawer(newVal, "postDetail");
    },
    "$store.state.drawer": function (newVal) {
      if (this.$store.state.drawerName == "postDetail") {
        this.commentEdit.commentDialog = newVal;
      }
    },
    "loadMethod"(v) {
      this.refreshData(this.mescroll)
    },
    "img.picDialog"(v) {
      this.$store.change_dialog(v, 'img.picDialog')
    },
    'shieldExplainDialog'(v) {
      this.$store.change_dialog(v, 'shieldExplainDialog')
    },
    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'img.picDialog')
        this.img.picDialog = v
      else if (this.$store.state.dialogName == 'shieldExplainDialog')
        this.shieldExplainDialog = v
    }
  },
  directives: {
    touch
  },
  beforeDestroy() {
    window.removeEventListener("native.keyboardshow", function (e) { });
  }
};
</script>

<style>
.mescroll_container_postDetail {
  top: 54px;
  position: fixed;
  bottom: 0;
  height: auto; /*如设置bottom:50px,则需height:auto才能生效*/
  width: 100%;
  background-color: #f5f5f5;
}
.light_thought {
  width: 100%;
  color: #e53935;
}

.hot_background_img {
  position: absolute;
  top: 36%;
  right: 36px;
}
.comment_to {
  background-color: #f5f5f5;
  border-radius: 10px;
  width: 90%;
}
.lucency {
  opacity: 0.6;
}
.grey_background {
  background-color: #f5f5f5;
}
</style>



